// MOBILE
// Region [DarkCyan] Site Header
@media screen and (max-width: 767px) {

    .desktop-only {
        display: none !important;
    }

    .desktop-only-block {
        display: none !important;
    }

    .mobile-only {
        display: flex !important;
    }

    .theme-switch-container {
        right: -0.75rem;
        top: 1rem;
    }

    #theme-toggle {
        right: 1rem;
        top: 2rem;
        height: 2rem;
        width: 2rem;
    }

    .button-container {
        padding: 0px 20px;
    }

    .button {
        padding: 5px;
        margin: 0px 50px;
        width: 80vw;
        height: 40px;
    }

    .next-button {
        position: relative;
        bottom: 25px;
        right: 0px;
    }

    .header {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        // .top-fill {
        //     height: 20vmax;
        // }
     
        .headline {
            font-size: 20vmin;
        }
   
    }

    .intro {
        .name {
            font-size: 4.5rem;
            margin: 0px 0px 0px -70px;
            padding-top: 10vh;
            width: 100vw;
           }
    
        .body {
            margin: 15vh 0px 0px 0px;
            width: 95vw;

            .line {
                padding: 1rem 0rem;
                width: 80%;
            }
    
            .one {
                font-size: 2.5rem;
               // margin-left: 5vw;
            }
    
            .two {
                font-size: 5vmin;
                margin-left: 20vw;
            }
    
            .three {
                font-size: 5vmin;
                margin-left: 25vw;
            }
    
            .four {
                font-size: 5vmin;
                margin-left: 20vw;
            }
        }
    }

    .photo-container {
        height: 100vh;
        z-index: -10;
    
        .img-headshot {
            height: 100vh;
            border: 0px;
            opacity: 0.25;
            filter: grayscale(0) brightness(1.5);
        }
        
    }

    // .photo-container {
    //     margin: 0px 0px;
    //     opacity: 0.5;
    //     width: 50vh;
    //     height: 50vh;

    //     .img-headshot {
    //         position: relative;
    //         top: -50px;
    //         left: 0px;
    //         height: 100vh;
    //         width: 100vw;
            
    //     }
    // }

    .project-number {
        justify-content: center;
        padding: 5vh 10px 600px 75vw;

        .text {
            font-size: 20vmin;
        }
    }

    .project-title {
        bottom: 50%;
        font-size: 7rem;
        left: 15px
    }

    .project-subtitle {
        left: 7%;
        bottom: 45vh;
    }

    .project-image {
        opacity: 0.75;

        img {
            object-fit: cover;
        }
    }

    .project-description {
        top: 55%;
    }

    .contact-container {
        height: 80vh;
        width: 100vw;

         .container {
            position: absolute;
            bottom: 60px;
            height: 80vh;
            width: 100%;
            justify-content: flex-start;
            align-items: center;
        }

        .contact-link-container {
            justify-content: flex-start;
            align-items: start;
            // height: 10rem;
            width: 95vw;
            
            .contact-link {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                width: 100%;
                height: 100%;
                transition: all 0.15s;

                .text {
                    font-size: 6rem;

                }
            }
        }

    

    }

    .footer-container {
        justify-content: space-evenly;

        .footer-text {
            color: var(--color-secondary);
            font-size: 0.85rem;
        }
    }

}
//endregion


